* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetic Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

  html,
  body,
  #potato,
